.page.fourohfour {
  display: none;
  max-width: 450px;
  margin-left: auto;
  margin-right: auto;
  img {
    margin: 1rem auto 2rem auto;
  }
}

.show:last-child {
  display: block;
}

#cart-overlay {
  position: fixed;
  display: block;
  top: 0; left: 0;
  width: 100%; height: 100%;
  background: rgba(246,242,239,0.8);
  -webkit-transition: opacity 200ms ease;
  transition: opacity 200ms ease;
  z-index: 10000;
  opacity: 0;
  pointer-events: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  user-select: none;
  .shopify-buy-cart-wrapper.is-active + & {
    opacity: 1;
    pointer-events: auto;
  }
}

#shopify {
  h1, h2, h3, h4, h5, p {
    padding: 0;
    margin: 0;
  }

  .page.fourohfour h1 {
    margin-bottom: 1.5rem;
  }

  select {
    text-rendering: auto !important;
  }

  ul {
    list-style: none;
    padding-left: 0;
    margin: 0;
  }

  img {
    display: block;
    max-width: 100%;
  }

  input {
    -webkit-appearance: textfield;
    margin: 0;
  }
}

*:focus {
  outline: none;
}

*::-moz-focus-inner {
  border: 0;
}

.clearfix:after {
  content: "";
